@charset "utf-8"; .row { padding: 0 10px; margin: 0 auto } 

/* header */
#header h1 { display: inline-block; position: absolute; top: 20px; left: 1%; color: rgba(0, 0, 0, .9); z-index: 20; transition:all .3s; } 
#header h1.scroll { transform: translateY(-10px) scale(90%); } 
.homeLogo.logo1.active { display: none; } 
#header .btn { position: absolute; top: 50%; right: 10px; transform: translateY(-50%); } 
#header { position:relative; } 
#header .headerFixed { position: fixed; top: 0; right: 0; left: 0; background: #fff; z-index: 9999; } 
#header .open { display: block; position: fixed; right: 50px; font-size: 30px; z-index: 999999999999999999; transform: translateY(-50%); cursor: pointer; } 
#header .row { position: relative } 

@keyframes cdAni { 
 0% { transform: translateX(1000px); opacity: 0; } 
 100% { transform: translateY(0); opacity: 1; } 
 }

/* open */
.cd-nav-trigger { position: absolute; top: 20px; right: 0%; width: 54px; /*background-color: #243040; */ height: 54px; overflow: hidden; border-radius: 0%; /* image replacement */ z-index: 30; background-color: rgba(255,255,255,0.9); text-indent: 100%; white-space: nowrap; -webkit-transition: -webkit-transform 0.5s; -moz-transition: -moz-transform 0.5s; transition: transform 0.5s; transition: .5s all; } 
.cd-nav-trigger.scroll { transform:translateY(-7px) } 
 .cd-nav-trigger .cd-nav-icon { /* icon created in CSS */
position: absolute; top: 50%; right: auto; bottom: auto; left: 50%; width: 30px; height: 2px; -webkit-transform: translateX(-50%) translateY(-50%); -moz-transform: translateX(-50%) translateY(-50%); -ms-transform: translateX(-50%) translateY(-50%); -o-transform: translateX(-50%) translateY(-50%); transform: translateX(-50%) translateY(-50%); background-color: #333; } 
.cd-nav-trigger .cd-nav-icon::before, .cd-nav-trigger .cd-nav-icon:after { /* upper and lower lines of the menu icon */ position: absolute; top: 0; right: 0; width: 100%; height: 100%; content: ''; background-color: inherit; /* Force Hardware Acceleration in WebKit */
-webkit-transform: translateZ(0); -moz-transform: translateZ(0); -ms-transform: translateZ(0); -o-transform: translateZ(0); transform: translateZ(0); -webkit-backface-visibility: hidden; backface-visibility: hidden; -webkit-transition: -webkit-transform 0.5s, width 0.5s, top 0.3s; -moz-transition: -moz-transform 0.5s, width 0.5s, top 0.3s; transition: transform 0.5s, width 0.5s, top 0.3s; } 
.cd-nav-trigger .cd-nav-icon::before { -webkit-transform-origin: right top; -moz-transform-origin: right top; -ms-transform-origin: right top; -o-transform-origin: right top; transform-origin: right top; -webkit-transform: translateY(-10px); -moz-transform: translateY(-10px); -ms-transform: translateY(-10px); -o-transform: translateY(-10px); transform: translateY(-10px); } 
.open.on .cd-nav-trigger .cd-nav-icon::before { width: 50%; -webkit-transform: translateY(0px) rotate(45deg); -moz-transform: translateY(0px) rotate(45deg); -ms-transform: translateY(0px) rotate(45deg); -o-transform: translateY(0px) rotate(45deg); transform: translateY(0px) rotate(45deg); } 
.cd-nav-trigger .cd-nav-icon::after { -webkit-transform-origin: right bottom; -moz-transform-origin: right bottom; -ms-transform-origin: right bottom; -o-transform-origin: right bottom; transform-origin: right bottom; -webkit-transform: translateY(10px); -moz-transform: translateY(10px); -ms-transform: translateY(10px); -o-transform: translateY(10px); transform: translateY(10px); } 
.open.on .cd-nav-trigger .cd-nav-icon::after { width: 50%; -webkit-transform: translateY(0px) rotate(-45deg); -moz-transform: translateY(0px) rotate(-45deg); -ms-transform: translateY(0px) rotate(-45deg); -o-transform: translateY(0px) rotate(-45deg); transform: translateY(0px) rotate(-45deg); } 


/* cd */
#header .cd { display:none; position: relative; top: 0; left: 0; width: 100%; height: 100%; z-index: 9999999; animation: cdAniOut 1.3s; background-color: #fff; } 
#header .cd.active { display: block; } 
#header .cd_full { width: 1300px; padding: 30px 15px; margin: 0 auto; animation: cdAni 1s; } 
#header .cd_nav { text-align: center; padding-bottom: 0 0 100px; } 
#header .cd_full h2 { position: relative; width: 200px; padding: 8px 0 50px; font-size: 24px; font-weight: bold; } 
.cd_full_L { float:left; width: 1000px; margin-right: 20px; } 
.cd_full_R { float:left; width: 178px; margin-top: 58px; border: 1px solid #ddd; } 
.cd_menu { display: inline-block;}

.cd_tab_list ul { display: table; width: 100%; table-layout: fixed; } 
.cd_tab_list li { display: table-cell; height: 60px; color: #777; font-size: 18px; line-height: 22px; border-bottom: 1px solid #ddd; box-sizing: border-box; text-align: center; vertical-align: middle; cursor: pointer; } 
.cd_tab_list li.on { border-bottom:1px solid #222; color:#222; font-weight: bold; } 
.all_category_block { display: none; margin-top: 40px } 
.all_category_block.on { display: block; } 
.tab_page_cont { display: table; width: 100%; table-layout: fixed; } 
.tab_page_cont li { display: table-cell; height: 60px; color: #777; font-size: 18px; border: 1px solid #ddd; background: #f7f7f7; line-height: 60px; border-left: none; border-bottom: 1px solid #222; box-sizing: border-box; text-align: center; cursor: pointer; } 
.tab_page_cont li:first-child { border-left: 1px solid #ddd; } 
.tab_page_cont li.on { color: #222; font-family: 'Noto_m'; border: 1px solid #222; background: #fff; border-bottom: 1px solid #fff; text-indent: -1px; } 
.tab_page_cont li a { display: block; width: 100%; height: 60px; } 
.tab_page_cont2 { display: table; width: 100%; table-layout: fixed; } 
.tab_page_cont2 li { display: table-cell; height: 60px; color: #777; font-size: 18px; border: 1px solid #ddd; background: #f7f7f7; line-height: 60px; border-left: none; border-bottom: 1px solid #222; box-sizing: border-box; text-align: center; cursor: pointer; } 
.tab_page_cont2 li:first-child { border-left: 1px solid #ddd; } 
.tab_page_cont2 li.on { color: #222; font-family: 'Noto_m'; border: 1px solid #222; background: #fff; border-bottom: 1px solid #fff; text-indent: -1px; } 
.tab_page_cont2 li a { display: block; width: 100%; height: 60px; } 
.tab_page { display:none } 
.tab_page.on { display:block } 
.tab_page2 { display:none } 
.tab_page2.on { display:block } 
.cont_tabs { margin-top: 35px; } 
.box_menu { display: inline-block; width: 180px; margin: 0 8px 48px; overflow: hidden; vertical-align: top; } 
.list_menu { display: inline-block; width: 178px; vertical-align: top; } 
.tit_list_block { padding: 0 0 10px; color: #222; font-size: 18px; overflow: hidden; min-height: 24px; margin-bottom: 10px; border-bottom: 1px solid #222; } 
.emp_blue { display: block; margin: 5px 0; color:#4567ad !important; padding-left: 10px } 
.list_menu a { display: block; color:#555; height: 30px; font-size: 14px; padding-left: 10px; line-height: 30px; } 

/* section */
#section.active { display: none; } 


/* nav 구역 */
#nav { position: absolute; top: 0px; left:0; width: 100%; padding:0 0 0 200px; z-index: 13; background-color: #fff; } 
#nav .navUl { width: 1400px; margin:0 auto; overflow: hidden; list-style-type: none; box-sizing: border-box; transition: all .1s } 
#nav .navUl.scroll { transform: translateY(-10px); } 
#nav .navUl.active { display: none; } 
#nav .navUl .menu { float: left; width: 14%; height:80px; text-align: center; } 
#nav .navUl .menu.active { float: left; width: 14%; height:100%; text-align: center; } 
.depth1 { display: block; padding: 0; padding: 0 10px; margin: 0 auto; font-size: 18px; letter-spacing: -0.3px; text-align: center; line-height: 100px; box-sizing: border-box; transition: all .1s; } 
.depth1.scroll { font-size:16px; } 
.depth1>a { transition: all 0.5s; } 
.depth1>a:hover { color: red } 
.depth2>ul { padding: 0; margin: 0; color: #888; list-style-type: none; } 
.depth2>ul>li>a { display: block; height: 70px; padding: 5px 0; border-radius: 5px 5px; transition: all 0.5s; line-height: 30px; } 
.depth2>ul>li>a:hover { color: red } 
#nav .navUl .depth2 { display: block; height:0px; font-size: 14px; overflow: hidden; background:#fff; transition: all 0.3s; } 
#nav .navUl .depth2.active { display: block; height:420px; transition: all 0.3s; } 

/* footer 구역 */